import { Projection } from 'ol/proj';

export const pixelProjection = new Projection({
  code: 'PIXELS',
  units: 'pixels',
  extent: [
    -40 * 512, 
    -8 * 512,
    3 * 512, 
    27 * 512
  ],
});

export const getTileExtent = (tile) => {
  const minX = tile.x * 512;
  const minY = (17 - tile.y - 1) * 512;
  const maxX = (tile.x + 1) * 512;
  const maxY = (17 - tile.y) * 512;

  return [minX, minY, maxX, maxY];
};
