// src/components/MapBackground.jsx
import React, { useEffect, useRef } from 'react';
import { Map, View } from 'ol';
import { addProjection } from 'ol/proj';
import ImageLayer from 'ol/layer/Image';
import ImageStatic from 'ol/source/ImageStatic';
import { pixelProjection, getTileExtent, tileData } from './map-utils';

const absoluteCenter = [-7424, 2816];

const MapBackground = ({ mapRef, setMap }) => {
  const mapInstanceRef = useRef(null);

  const createImageLayer = (tile) => {
    const extent = getTileExtent(tile);
    try {
      const imageStaticSource = new ImageStatic({
        url: tile.url,
        imageExtent: extent,
        projection: pixelProjection,
        //crossOrigin: 'anonymous',
        imageLoadFunction: (image, src) => (image.getImage().src = src),
      });
      return new ImageLayer({ source: imageStaticSource, zIndex: 10 });
    } catch (error) {
      console.error(`Error creating ImageLayer for tile ${tile.url}:`, error);
      return null;
    }
  };

  useEffect(() => {
    addProjection(pixelProjection);
    if (mapRef.current && mapRef.current.olMap) return;

    const mapInstance = new Map({
      target: mapRef.current,
      layers: tileData.map(createImageLayer).filter(Boolean),
      view: new View({
        projection: pixelProjection,
        center: absoluteCenter,
        zoom: 3,
        minZoom: 2,
        maxZoom: 8,
      }),
    });

    mapRef.current.olMap = mapInstance;
    setMap(mapInstance);
    mapInstanceRef.current = mapInstance;
  }, [mapRef, setMap]);

  return <div ref={mapRef} className='map-container' />;
};

export default MapBackground;
